.Portfolio-tab {
    background-color: white;
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
    border: 1px solid;
    box-shadow: -2px 2px 5px 0px rgba(0, 0, 0, 0.75);
    padding: 10px;
    display: inline-block;
    width: 420px;
}
.PortfolioTab-btns {
    display: inline-block;
    content-align: center;
}
.PortfolioTab-delete {
    background-color: DodgerBlue; /* Blue background */
    color: white; /* White text */
    border: none;
    font-size: 16px; /* Set a font size */
    cursor: cursor; /* Mouse pointer on hover */
    border-radius: 25px;
    /* Remove borders */
    margin-left: 7px;
    margin-right: 5px;
}
.PortfolioTab-delete:hover {
    background-color: red;
}
.PortfolioTab-edit {
    background-color: DodgerBlue; /* Blue background */
    color: white; /* White text */
    border: none;
    font-size: 16px; /* Set a font size */
    cursor: cursor; /* Mouse pointer on hover */
    border-radius: 25px;
    /* Remove borders */
}
.PortfolioTab-edit:hover {
    background-color: DodgerBlue;
}
p {
    display: block;
    margin-left: 10px;
    margin-right: 10px;
    overflow-wrap: break-word;
    text-align: center;
}

.PortfolioTab-editBox {
    width: 80px;
}

.red {
    color: red;
}
.green {
    color: green;
}
